<template>
  <div v-bind:id="plot_id"></div>
</template>

<script>
import {ProfilePlotCanvas} from "../ploting/__target__/canvas.js"

export default {
  name: 'ProfilePloter',
  props: {
      profiles: Array,
  },
  data: function() {
      return {
          "plot_id": "plots",
      }
  },
  methods: {
      plot(profiles) {
        let profile = profiles[1]
        let fig = ProfilePlotCanvas(profile).figure;
        Plotly.react(this.plot_id, fig.data, fig.layout, {responsive: true});
      }
  }
}
</script>

