<template>
  <div id="app">
    <file-upload v-on:fileupload-success="updatePloter($event)"></file-upload>
    <profile-ploter v-bind:profiles="ploterData"></profile-ploter>
  </div>
</template>

<script>
import FileUpload from './components/FileUpload.vue'
import ProfilePloter from './components/ProfilePloter.vue'

export default {
  name: 'app',
  data: function(){
    return {
      ploterData: [],
    }
  },
  components: {
    FileUpload,
    ProfilePloter,
  },
  methods: {
    updatePloter(event){
      alert(JSON.stringify(event));
      return;
      var data = event.data
      if (!data) {
          alert("No data");
      } else {
        this.ploterData = data;
      }
    }
  }
}
</script>

